html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

img {
  display: inline-block;
}

*:focus {
  outline: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--top-bg);
  color: var(--bold-text);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
}

[data-theme=light] {
  --toggle-bg1: hsl(230, 22%, 74%);
  --toggle-bg2: hsl(230, 22%, 74%);
  --btn-bg: hsl(0, 0%, 100%);
  --top-bg: hsl(0, 0%, 100%);
  --card-bg: hsl(228, 50%, 96%);
  --text: hsl(228, 12%, 44%);
  --bold-text: hsl(230, 17%, 14%);
  --card-hover: hsl(228, 33%, 91%);
}

[data-theme=dark] {
  --toggle-bg1: hsl(210, 78%, 56%);
  --toggle-bg2: hsl(146, 68%, 55%);
  --btn-bg: hsl(230, 17%, 14%);
  --top-bg: hsl(232, 19%, 15%);
  --card-bg: hsl(228, 28%, 20%);
  --text: hsl(228, 34%, 66%);
  --bold-text: hsl(0, 0%, 100%);
  --card-hover: hsl(228, 25%, 27%);
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  justify-content: center;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, auto);
  }
}

.header {
  margin-top: 2.25rem;
  display: flex;
}
@media (min-width: 40em) {
  .header {
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
  }
}
@media (max-width: 39.9375em) {
  .header {
    flex-flow: column wrap;
    gap: 1.25rem;
  }
  .header__toggle {
    align-self: center;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text);
}

.toggle {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  grid-template-rows: repeat(2, max-content);
  align-items: center;
  gap: 0.28125rem 0.5rem;
  border: none;
  margin: 0;
}
.toggle label {
  font-size: 0.875rem;
  font-weight: 600;
  padding: 0.5rem;
  border: 5px inset transparent;
  cursor: pointer;
}
.toggle label[for=dark] {
  grid-column: 1/2;
  justify-self: end;
}
.toggle label[for=light] {
  grid-column: 3/4;
}
.toggle label:hover {
  border: 5px inset var(--bold-text);
  border-radius: 0.75rem;
}
.toggle__wrapper {
  height: 1.5rem;
  width: 3rem;
  grid-column: 2/3;
  grid-row: 1/2;
  cursor: pointer;
  position: relative;
  display: flex;
  justify-content: center;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0 -2px;
  width: 1.25rem;
  height: 1.5rem;
}
.toggle__background {
  height: 100%;
  border-radius: 0.75rem;
  overflow: hidden;
  display: block;
  background: linear-gradient(225deg, var(--toggle-bg1) 0%, var(--toggle-bg2) 98.02%);
  position: absolute;
  top: 0;
  width: 100%;
}
.toggle__button {
  position: absolute;
  top: 0.1875rem;
  left: 0.1875rem;
  right: 100%;
  height: 1.125rem;
  width: 1.125rem;
  border-radius: 50%;
  background-color: var(--btn-bg);
  transition: 200ms all ease-in-out;
}
.toggle__button:focus {
  outline: 2px solid var(--bold-text);
}

.card {
  background-color: var(--card-bg);
  color: var(--text);
  min-width: 15.9375rem;
  display: flex;
  flex-flow: column nowrap;
  text-align: center;
  padding: 1.5625rem;
  border-radius: 0.375rem;
  transition: all 150ms ease-in-out;
}
.card:hover {
  background-color: var(--card-hover);
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--instagram {
  position: relative;
}
.card--instagram::before {
  background: linear-gradient(225deg, var(--instagram-start), var(--instagram-middle), var(--instagram-end));
  content: "";
  display: block;
  height: 0.3125rem;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  border-top-right-radius: 0.3125rem;
  border-top-left-radius: 0.3125rem;
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card__platform {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.625rem;
  padding: 1.25rem 0 0.9375rem 0;
}
.card__username {
  font-size: 0.8rem;
}
.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: start;
  align-items: start;
  gap: 1.25rem;
}
.card-grid .card__change {
  justify-self: end;
  align-self: end;
  padding: 0 0 0.125rem 0;
}
.card-grid__img {
  justify-self: end;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 600;
}
.card__count {
  font-size: 3.5rem;
  font-weight: bold;
  color: var(--bold-text);
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  font-size: 0.8rem;
  font-weight: 300;
  color: var(--text);
  letter-spacing: 0.3125rem;
}
.card__change {
  font-size: 0.8rem;
  font-weight: 600;
  padding: 1.25rem 0 1.25rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.3125rem;
}
.card__change--increase {
  color: var(--limegreen);
}
.card__change--decrease {
  color: var(--brightred);
}

.h2 {
  display: block;
  margin: 0 auto;
}